<template>
  <div
    class="progress rounded-5 my-2"
    style="background-color: white; height: 24px"
    role="progressbar"
    aria-label="Example with label"
    :aria-valuenow="vlaueProgress"
    aria-valuemin="0"
    aria-valuemax="100"
  >
    <div
      style="font-family: 'Cinzel', serif"
      :ref="`${vlaueProgress}-${index}`"
      class="progress-bar text-end fw-bold"
      :style="`width:${valueWidth}%;transition:1s`"
    >
      <span class="pe-3">{{ vlaueProgress }}%</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "progressSkill",
  props: {
    vlaueProgress: String,
    index: Number,
  },
  data() {
    return {
      valueWidth: 0,
    };
  },
  methods: {
    reveal() {
      const reveal = this.$refs[`${this.vlaueProgress}-${this.index}`];
      const windowheight = window.innerHeight;
      if (reveal) {
        const revealtop = reveal.getBoundingClientRect().top;
        const revealpoint = 20;
        revealtop < windowheight - revealpoint
          ? (this.valueWidth = this.vlaueProgress)
          : (this.valueWidth = 0);
      }
    },
  },
  mounted() {
    window.addEventListener("scroll", this.reveal);
  },
};
</script>
<style scoped>
.progress-bar {
  background: linear-gradient(
    90deg,
   var(--dark-green) 0%,
    var(--green-color) 100%
  );
}
</style>
