<template>
  <div :id="id" class="carousel slide">
    <div class="carousel-inner d-flex align-items-center">
      <div
        class="carousel-item"
        v-for="(image, index) in images"
        :key="index"
        :class="{ active: index == 0 }"
      >
        <span class="d-flex align-items-center" style="min-height: 100vh">
          <img :src="image" class="d-block m-auto img-fluid"
        /></span>
      </div>
    </div>
    <buton
      class="carousel-control-prev position-fixed top-0"
      type="button"
      :data-bs-target="`#${id}`"
      data-bs-slide="prev"
    >
      <span class="fs-1" style="color: #000000d6"
        ><i class="fa-solid fa-circle-left"></i
      ></span>
    </buton>
    <button
      class="carousel-control-next position-fixed top-0"
      type="button"
      :data-bs-target="`#${id}`"
      data-bs-slide="next"
    >
      <span class="fs-1" style="color: #000000d6"
        ><i class="fa-solid fa-circle-right"></i
      ></span>
    </button>
  </div>
</template>

<script>
export default {
  name: "carouselImage",
  props: {
    images: Array,
    id: String,
  },
};
</script>

